import { fetchData } from './apiUtils';

const loader = document.querySelector('.loader');

const fetchPopularMovies = async page => {
  try {
    // Afieaz loader-ul i seteaz un delay minim de 1 secund
    loader.style.display = 'block';
    const timeout = new Promise(resolve => setTimeout(resolve, 1000));

    const params = { page: page };
    const data = await Promise.all([
      fetchData('/trending/movie/week', params),
      timeout, // Ateapt minim 1 secund, indiferent de ct dureaz cererea
    ]);

    // Ascunde loader-ul dup ce datele au fost primite
    loader.style.display = 'none';

    return data[0]; // Returneaz doar rezultatul cererii API i nu timeout-ul
  } catch (error) {
    console.error('Error fetching popular movies:', error);
    loader.style.display = 'none'; // n caz de eroare, ascunde loader-ul
    // Poi aduga un mesaj de eroare sau tratarea erorii aici
    throw error;
  }
};

const fetchMovieDetails = async movieId => {
  const params = { language: 'en-US' };
  return fetchData(`/movie/${movieId}`, params); // Efectueaz cererea pentru detalii de film
};

const fetchMoviesDetails = async ids => {
  const arrayOfPromises = ids.map(async id => {
    return await fetchMovieDetails(id);
  });
  const movies = await Promise.all(arrayOfPromises);
  return movies;
};

export { fetchPopularMovies, fetchMovieDetails, fetchMoviesDetails };
