const getGalleryElement = () => document.querySelector('.gallery'); // Funcia getGalleryElement returneaz elementul cu clasa CSS "gallery"
const getMyLibraryGalleryElement = () =>
  document.querySelector('.my-library-gallery');

// Funcia clearGallery se ocup de tergerea coninutului galeriei

const clearGallery = () => {
  const galleryElement = getGalleryElement(); // Obine elementul galeriei utiliznd funcia getGalleryElement
  // Verific dac s-a gsit elementul galeriei
  if (galleryElement) {
    galleryElement.innerHTML = ''; // Dac exist, terge toate elementele din galerie
  }
};

const clearMyLibraryGallery = () => {
  const myLibraryGalleryElement = getMyLibraryGalleryElement(); // Obine elementul galeriei utiliznd funcia getGalleryElement
  // Verific dac s-a gsit elementul galeriei
  if (myLibraryGalleryElement) {
    myLibraryGalleryElement.innerHTML = ''; // Dac exist, terge toate elementele din galerie
  }
};
export {
  clearGallery,
  clearMyLibraryGallery,
  getGalleryElement,
  getMyLibraryGalleryElement,
};
